<template>
  <div>
    <div class="seller" ref="seller">
      <div class="seller-content">
        <div class="overview">
          <h1 class="title">
            {{seller.name}}
          </h1>
          <div class="desc">
            <star :size="36" :score="seller.score"></star>
            <span class="text">({{seller.ratingCount}})</span>
            <span class="text">月售{{seller.sellCount}}单</span>
          </div>
          <ul class="remark">
            <li class="block">
              <h2>起送价</h2>
              <div class="content">
                <span class="stress">{{seller.minPrice}}</span>元
              </div>
            </li>
            <li class="block">
              <h2>商家配送</h2>
              <div class="content">
                <span class="stress">{{seller.deliveryPrice}}</span>元
              </div>
            </li>
            <li class="block">
              <h2>平均配送时间</h2>
              <div class="content">
                <span class="stress">{{seller.deliveryTime}}</span>分钟
              </div>
            </li>
          </ul>
          <div class="favorite" @click="toggleFavorite($event)">
            <span class="icon-favorite" :class="{'active':favorite}"></span>
            <span class="text">{{favoriteText}}</span>
          </div>
        </div>
        <split></split>
        <div class="bulletin">
          <h1 class="title">公告与活动</h1>
          <div class="content-wrapper border-1px">
            <p class="content">{{seller.bulletin}}</p>
          </div>
          <ul class="support" v-if="seller.supports">
            <li v-for="(item,index) of seller.supports" class="support-item border-1px" :key="index">
              <span class="icon" :class="classMap[item.type]">{{index}}</span>
              <span class="text">{{item.description}}</span>
            </li>
          </ul>
        </div>
        <split></split>
        <div class="pics">
          <h1 class="title">商家实景</h1>
          <div class="pic-wrapper" ref="picWrapper">
            <ul class="pic-list" ref="picList">
              <li class="pic-item" v-for="(pic,index) in seller.pics" :key="index">
                <img :src="pic" width="120" height="90" alt="pic">
              </li>
            </ul>
          </div>
        </div>
        <split></split>
        <div class="info">
          <h1 class="title">商家信息</h1>
          <ul>
            <li class="info-item" v-for="(info,index) in seller.infos" :key="index">
              {{info}}
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import star from 'components/star/star';
import split from 'components/split/split';
import BScroll from 'better-scroll';
import {saveToLocal, loadFromLocal} from 'common/js/store.js';
export default {
  props: {
    seller: {
      type: Object
    }
  },
  data() {
    return {
      favorite: (() => loadFromLocal(this.seller.id, 'favorite', false))()
    };
  },
  computed: {
    favoriteText() {
      return this.favorite ? '已收藏' : '收藏';
    }
  },
  created() {
      this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
    },
  mounted() {
    this._initScroll();
    this._initPics();
  },
  watch: {
    'seller'() {
      this._initScroll();
      this._initPics();
    }
  },
  methods: {
    toggleFavorite(event) {
      if (!event._constructed) {
        return;
      }
        this.favorite = !this.favorite;
        saveToLocal(this.seller.id, 'favorite', this.favorite);
    },
    _initScroll() {
        this.$nextTick(() => {
          if (!this.scroll) {
            this.scroll = new BScroll(this.$refs.seller, {
            click: true
            });
          } else {
            this.scroll.refresh();
          }
        });
    },
    _initPics() {
      if (this.seller.pics) {
        let picWidth = 120;
        let margin = 6;
        let width = (picWidth + margin) * this.seller.pics.length - margin;
        this.$refs.picList.style.width = width + 'px';
      }
      this.$nextTick(() => {
        if (!this.picScroll) {
          this.picScroll = new BScroll(this.$refs.picWrapper, {
            scrollX: true,
            eventPassthrough: 'vertial'
          });
        } else {
          this.picScroll.refresh();
        }
      });
    }
  },
  components: {
    star,
    split
  }
};
</script>
<style lang="stylus" rel="stylesheet/stylus">
@import '../../common/stylus/mixin.styl';
  .seller
    position: absolute
    top: 174px
    bottom: 0
    left: 0
    width :100%
    overflow : hidden
    .overview
      padding: 18px
      .title
        font-size: 14px
        font-weight: 700
        color: rgb(7,17,27)
        line-height 14px
        margin-bottom: 8px
      .desc
        font-size: 0
        padding-bottom: 18px;
        border-1px(rgba(7,17,27,0.1))
        .star
          display: inline-block
          vertical-align: top
          margin-right: 8px
        .text
          display: inline-block
          line-height: 18px
          font-size: 10px
          vertical-align: top
          margin-right: 12px
          color: rgb(77,85,93)
      .remark
        display: flex
        padding-top: 18px
        .block
          flex: 1
          text-align: center
          border-right: 1px solid rgba(7,17,27,0.1)
          &:last-child
            border-right: none
          h2
            margin-bottom: 4px
            line-height: 10px
            font-size: 10px
            color: rgb(147,153,159)
          .content
            line-height: 24px
            font-size: 10px
            color: rgb(7,17,27)
            .stress
              font-size: 24px
      .favorite
        width: 50px
        position: absolute
        right: 11px
        top: 18px
        text-align: center
        .icon-favorite
          display: block
          margin-bottom: 4px
          color: #d4d6d9
          line-height: 24px
          font-size: 24px
          &.active
            color: rgb(240,20,20)
        .text
          display: block
          line-height:10px
          font-size: 10px
          color: rgb(77,85,93)
    .bulletin
      padding: 18px 18px 0 18px
      .title
        font-size: 14px
        font-weight: 700
        color: rgb(7,17,27)
        line-height 14px
        margin-bottom: 8px
      .content-wrapper
        padding: 0 12px 16px 12px
        border-1px(rgba(7,17,27,0.1))
        .content
          line-height: 24px
          font-size: 12px
          color: rgb(240,20,20)
      .support
        .support-item
          padding: 16px 12px
          border-1px(rgba(7,17,27,0.1))
          font-size: 0
          &:last-child
            border-none()
          .icon
            display: inline-block
            margin-right: 6px
            width: 16px
            height :16px
            background-size: 16px 16px
            background-repeat: no-repeat
            vertical-align: top
            &.decrease
              bg-image("decrease_4")
            &.discount
              bg-image("discount_4")
            &.guarantee
              bg-image("guarantee_4")
            &.invoice
              bg-image("invoice_4")
            &.special
              bg-image("special_4")
          .text
            line-height: 16px
            font-size: 12px
            color: rgb(7,17,27)
    .pics
      padding: 18px
      .title
        font-size: 14px
        font-weight: 700
        color: rgb(7,17,27)
        line-height 14px
        margin-bottom: 8px
      .pic-wrapper
        width: 100%
        overflow: hidden
        white-space: nowrap
        .pic-list
          font-size: 0
          .pic-item
            display: inline-block
            margin-right: 6px
            width:120px
            height: 90px
            &:last-child
              margin-right: 0
    .info
      padding: 18px 18px 0 18px
      color: rgb(7,17,27)
      .title
        padding-bottom: 12px
        font-weight: 700
        line-height: 14px
        border-1px(rgba(7,17,27,0.1))
        font-size: 14px
      .info-item
        padding: 16px 12px
        line-height: 16px
        border-1px(rgba(7,17,27,0.1))
        font-size: 12px
        &:last-child
          border-none()
</style>



